.purchase {
  padding: 65px 25px 120px;
  background-image: linear-gradient(305deg, $purple-blue, $royal);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  h1 {
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    color: $white;
    text-transform: uppercase;
    margin-bottom: 45px; }
  &__block {
    border-radius: 20px;
    background-color: $white;
    padding: 30px;
    input {
      font-size: 21px;
      font-weight: normal;
      color: $dark;
      @include placeholder-color($dark);
      padding: 15px 0;
      margin-bottom: 25px;
      border-bottom: 1px solid $dark;
      width: 100%;
      font-family: 'Eina03'; }
    &__select {
      margin-bottom: 25px; }
    .selectize-input {
      box-shadow: none;
      border: none;
      border-bottom: 1px solid $dark;
      border-radius: 0;
      padding: 15px 0; }
    .selectize-input>* {
      font-size: 21px;
      font-weight: normal;
      color: $dark; }
    .selectize-control.single .selectize-input input {
      display: none !important; } } }
